@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:'';content:none}q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{font-family:'Roboto', sans-serif}body{background:#737373}.container{max-width:1260px;margin:0 auto}

.hero{background-image:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/dasinua/air-quality2.git/static/media/hero_bg.06b732d1.jpg);background-position:50% 0%;padding:0 0 178px}.hero__partners{display:flex;align-items:center}.hero__partners-item{margin-left:60px}.hero__partners-item:first-child{margin-left:-33px}.hero__navigation-item{text-decoration:none;font-style:normal;font-weight:normal;font-size:19px;line-height:22px;color:#FFFFFF;margin-left:80px;transition:.3s ease-in}.hero__navigation-item:first-child{margin-left:0}.hero__navigation-item:hover{color:#B78A3F}.hero__title{width:664px;font-weight:normal;font-size:57px;line-height:67px;color:#fff;margin:108px 0 132px}.hero__link{display:inline-block;font-weight:normal;font-size:32px;line-height:37px;text-decoration:none;color:#fff;background:#B78A3F;padding:32px 154px;transition:.3s ease-in}.hero__link:hover{background:#8a6830}

.facts{box-sizing:border-box;position:relative;padding:100px 60px 50px}.facts__title{font-weight:normal;font-size:35px;line-height:41px;color:#fff;text-align:center;margin:0 0 40px 0}.facts__slide-desc{font-weight:normal;font-size:27px;line-height:32px;text-align:center;color:#FFFFFF;margin-top:35px}.facts>.swiper-button-prev,.facts .swiper-button-next{color:#B78A3F}.facts>.swiper-button-prev{left:10px}.facts>.swiper-button-next{right:10px}.facts>.swiper-button-disabled{opacity:0}.facts__pagination{display:flex;justify-content:center;margin-top:5px}.facts__pagination-bullet{display:inline-block;width:20px;height:20px;border-radius:50%;background-color:transparent;border:2px solid #B78A3F;margin:0 20px;opacity:1;transition:0.3s background-color}.facts__pagination>.swiper-pagination-bullet-active{background-color:#B78A3F}

.air-q{padding:50px 0}.air-q__title{text-align:center;font-weight:normal;font-size:35px;line-height:41px;color:#fff}.air-q__map{height:560px;margin:42px 0}.air-q__text{font-weight:normal;font-size:23px;line-height:27px;color:#fff}

.sectors{padding:50px 0}.sectors__title{font-weight:normal;font-size:35px;line-height:41px;color:#FFFFFF;text-align:center}.sectors__subtitle{font-weight:normal;font-size:23px;line-height:27px;color:#FFFFFF;margin:42px 0 30px}.sectors__list{display:flex;flex-wrap:wrap;justify-content:space-between}.sectors__list-item{position:relative;width:calc((100% / 3) - 70px);height:240px;display:flex;align-items:center;justify-content:center;margin-bottom:30px}.sectors__list-item-name{font-weight:normal;font-size:23px;line-height:30px;color:#222222;width:100%;height:30px;background:rgba(255,255,255,0.83);text-align:center}.sectors__list-item-tooltip{position:absolute;left:100%;width:440px;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background:#DADADA;padding:16px 16px 16px 75px;-webkit-clip-path:polygon(7% 0, 100% 0, 100% 50%, 100% 100%, 8% 100%, 0% 50%);clip-path:polygon(7% 0, 100% 0, 100% 50%, 100% 100%, 8% 100%, 0% 50%);opacity:1;z-index:1}.sectors__list-item-tooltip-switched{left:auto;left:initial;right:100%;padding:16px 75px 16px 16px;-webkit-clip-path:polygon(0 0, 95% 0, 100% 50%, 95% 100%, 0 100%, 0% 50%);clip-path:polygon(0 0, 95% 0, 100% 50%, 95% 100%, 0 100%, 0% 50%)}

.footer{border-top:1px solid #FFFFFF}.footer__partners{display:flex;align-items:center}.footer__partners-item{margin-right:120px}.footer__partners-item:last-child{margin-right:0}.footer__text-wrap{display:flex;justify-content:space-between}.footer__nav{display:flex;flex-direction:column;margin-bottom:88px}.footer__nav-link{font-weight:normal;font-size:17px;line-height:20px;text-decoration:none;color:#fff;margin-bottom:20px}.footer__nav-link:last-child{margin-bottom:0}.footer__nav-text{width:366px;font-weight:normal;font-size:17px;line-height:20px;color:#fff}.footer__text{font-weight:300;font-size:14px;line-height:14px;color:#fff;margin-bottom:14px}.text-yellow{color:#EDE852}

